import React, { Fragment,useState } from 'react'
import {Link} from "react-router-dom"
import { toast } from 'react-toastify';
import "./Login.css"

function Login({setAuth}) {

    const [inputs,setInputs]=useState({
        email : "",
        password : ""
    })

    const {email,password,name} = inputs;
    const onChange =(e)=>{
        setInputs({...inputs,[e.target.name]  : e.target.value })
    }   

    const onSubmitForm= async (e)=>{
        e.preventDefault()
        try {
            const body = {email,password,name}
            const response = await fetch("http://localhost:3002/auth/login",{
                method :"POST",
                headers : {"Content-Type" : "application/json"},
                body  : JSON.stringify(body)
            })

            const parseRes = await response.json()
            if(parseRes.token){
                localStorage.setItem("token", parseRes.token)
                setAuth(true)
                parseRes.message === "Password or Email is incorrect" ?  setAuth(false) : setAuth(true)
                toast.success("Login successfully !!")
            }else{
                toast.error(parseRes.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    })
            }
   
        } catch (error) {
            console.log(error.message);
        }
    }
    return (
    <Fragment>
        <div class="container login-container">
            <h1>Login</h1>
            <form onSubmit={onSubmitForm} >
                <input type="email" name="email"  placeholder="email"  className="form-control my-3"  value={email} onChange={e=>onChange(e)} required/>
                <input type="password" name="password"  placeholder="password"  className="form-control my-3"  value={password}  onChange={e=>onChange(e)} required/>
                <button className="btn btn-success btn-block">Login</button>
            </form>
            <br></br>
            <br></br>
            <br></br>
          <p>Not Registered ?   <Link to="/register">Register an account</Link> </p>
            <div  className="socialmedia__Icons">
               <a href="/register" class=" socialMedia__Icon1 ">
                    <i class="fab fa-google "> </i> 
                </a>
                <a href="/register" class=" socialMedia__Icon2">
                <i class="fab fa-twitter" aria-hidden="true"></i>
                    </a>
                    <a href="/register" class=" socialMedia__Icon3">
                    <i class="fab fa-facebook" aria-hidden="true"></i>
                </a> 
                <a href="/register" class=" socialMedia__Icon4">
                <i class="fab fa-instagram" aria-hidden="true"></i>
                </a> 
                <a href="/register" class=" socialMedia__Icon5">
                <i class="fab fa-whatsapp" aria-hidden="true"></i>
                </a> 
            </div>
        </div>
    </Fragment>
    )
}

export default Login
